.adress {
    p {
        margin-top: 0;
        margin-bottom: 0;
        font-size: 10px;
        line-height: 140%;
        color: var(--textColor);
    }
}

.adress__name {
    margin-top: 0;
    margin-bottom: 16px;
    font-size: 10px;
    line-height: 140%;
    color: var(--textColor);   
}

.adress__email {
    margin-top: 0;
    margin-bottom: 0;
    font-size: 10px;
    line-height: 140%;
    color: var(--textColor);
    text-decoration: none;
    transition: color 0.3s ease-in-out;

    &:hover,
    &:focus {
        color: black;
    }
}

@media (min-width: 768px) {
    .adress p, .adress__email, .adress__name {
        font-size: 14px;
        line-height: 143%;
      }
}