.top__wrapper {
    padding-top: 20px;

    &:not(&:last-child) {
        margin-bottom: 100px;
    }
}

.top__title {
    margin-top: 0;
    margin-bottom: 30px;
    font-weight: 600;
    font-size: 24px;
    line-height: 133%;
    color: var(--dark-blue);
}

.top__text {
    margin-top: 0;
    font-size: 14px;
    line-height: 143%;
    color: var(--textColor);

    p {
        margin-top: 0;
        margin-bottom: 10px;
    }
}

.top__text--main {
    margin-bottom: 23px;
    font-size: 16px;
    line-height: 150%;    
}

.top__list {
    margin: 0;
    padding: 0;
    list-style: none;
}

.top__list-item {
    position: relative;
    padding-left: 39px;

    &:not(:last-child) {
        margin-bottom: 26px;
    }

    &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        display: block;
        width: 24px;
        height: 24px;
        background-image: url('/img/icon-tick.svg');
        background-repeat: no-repeat;
    }
}

.top__content {
    margin-bottom: 22px;
}

.top {
    margin-bottom: 100px;
}

@media (min-width: 768px) {
    .top__wrapper:nth-child(2n) .top__img-wrapper {
        padding-right: 12px;
    }

    .top__wrapper:nth-child(2n+1) .top__img-wrapper {
        padding-left: 12px;
    }
}

@media (min-width: 992px) {
    .top {
        padding-top: 132px;
        margin-bottom: 150px;
    }
    .top__wrapper {
        padding-top: 0;
    }

    .top__title {
        margin-bottom: 40px;
        font-size: 32px;
        line-height: 1;
    }

    .top__text--main {
        margin-bottom: 23px;
        font-size: 20px;
        line-height: 150%;
    }

    .top__text {
        font-size: 16px;
        line-height: 150%;
    }

    .top__list {
        font-size: 16px;
        line-height: 150%;
    }

    .top__content {
        margin-bottom: 0;
    }

    .top__wrapper:not(.top__wrapper:last-child) {
        margin-bottom: 185px;
    }

    .top__wrapper:nth-child(2n+1) .top__content {
        padding-right: 69px;
    }

    .top__wrapper:nth-child(2n) .top__content {
        padding-left: 69px;
    }
}