.socials {
    grid-gap: 14px;
}

.social__item {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 24px;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    color: var(--textColor);
    text-decoration: none;
    background-color: var(--gray-hell);
    transition: background-color 0.3s ease-in-out;

    &:hover,
    &:focus {
        background-color: var(--blue-light);
    }
}