:root {
  --textColor: #4D5769;
  --gray: #D6DEEC;
  --gray-hell: #EDF1F8;
  --gray-m: #e5e5e5;
  --gray-dark: #363E4B;
  --white: #ffffff;
  --blue: #5096FF;
  --blue-light:#AFC9FF;
  --blue-middle: #3B5998;
  --dark-blue: #0F2C46;
  --box-shadow: 0px 8px 37px rgba(0, 0, 0, 0.07);
  --background-color: #E5E5E5;
}

html {
  min-height: 100vh;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

input[type=number] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

@font-face {
  font-display: swap;
  font-family: "Inter";
  src: local("Inter-Regular"), url("../fonts/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-display: swap;
  font-family: "Inter";
  src: local("Inter-Medium"), url("../fonts/Inter-Medium.woff2") format("woff2"), url("../fonts/Inter-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-display: swap;
  font-family: "Inter";
  src: local("Inter-Bold"), url("../fonts/Inter-Bold.woff2") format("woff2"), url("../fonts/Inter-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 143%;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 400;
  color: var(--textColor);
  font-style: normal;
  background: var(--white);
}

main {
  flex-grow: 1;
  background-color: var(--white);
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.body {
  position: relative;
}

.table-responsive {
  overflow: hidden;
  overflow-x: auto;
}

.container-xl {
  max-width: 1260px;
  padding-left: 32px;
  padding-right: 32px;
}

.socials {
  grid-gap: 14px;
}

.social__item {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 24px;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  color: var(--textColor);
  text-decoration: none;
  background-color: var(--gray-hell);
  transition: background-color 0.3s ease-in-out;
}
.social__item:hover, .social__item:focus {
  background-color: var(--blue-light);
}

.header {
  position: relative;
}

.header__container {
  padding-top: 20px;
  padding-bottom: 20px;
}

@media (max-width: 991px) {
  .header__toggler {
    width: 23px;
    height: 23px;
    padding: 0;
    margin: 0;
    background-color: transparent;
    border: 0;
  }
  .header__toggler > span {
    position: relative;
    display: grid;
    grid-gap: 4px;
    align-content: center;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
  }
  .header__toggler > span span {
    display: block;
    width: 23px;
    height: 2px;
    background-color: var(--textColor);
  }

  .header__menu-wrapper {
    position: fixed;
    top: 0;
    left: -300px;
    z-index: 1;
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 92px;
    width: 257px;
    min-height: 100vh;
    background-color: var(--white);
    transition: left 0.3s ease-in-out;
  }

  .header__logo {
    width: 146px;
    height: 31px;
  }

  .header__menu {
    margin-bottom: 48px;
  }

  .header__mobile p {
    margin-top: 0;
    margin-bottom: 4px;
    color: var(--blue-middle);
  }
  .header__mobile a {
    display: block;
    margin-bottom: 4px;
    color: var(--blue-middle);
    text-decoration: none;
    transition: color 0.3s ease-in-out;
  }
  .header__mobile a:hover, .header__mobile a:focus {
    color: var(--dark-blue);
  }

  .header.opened .header__menu-wrapper {
    left: 0;
  }
  .header.opened .header__logo {
    z-index: 3;
    width: 86px;
    height: 21px;
    margin-left: 43px;
  }
  .header.opened .header__toggler {
    position: fixed;
    top: 23px;
    left: 28px;
    z-index: 3;
  }
  .header.opened .header__toggler > span span:nth-child(1) {
    display: none;
  }
  .header.opened .header__toggler > span span:nth-child(2) {
    position: absolute;
    top: 9px;
    left: 2px;
    width: 20px;
    z-index: 3;
    transform: rotate(45deg);
  }
  .header.opened .header__toggler > span span:nth-child(3) {
    position: absolute;
    top: 9px;
    right: 1px;
    width: 20px;
    z-index: 3;
    transform: rotate(-45deg);
  }
}
@media (min-width: 992px) {
  .header {
    box-shadow: var(--box-shadow);
  }

  .header__container {
    padding-top: 21px;
    padding-bottom: 12px;
  }

  .header__logo {
    width: 243px;
    height: 57px;
  }
}
.footer {
  background-color: var(--white);
  box-shadow: 0px 8px 36px rgba(0, 0, 0, 0.14);
}

.footer__wrapper {
  padding-top: 40px;
  padding-bottom: 37px;
}

.footer__adress {
  padding-top: 40px;
  padding-bottom: 48px;
  border-top: 2px solid var(--gray-hell);
}

.footer__menu {
  margin-bottom: 33px;
}

@media (min-width: 768px) {
  .footer__adress {
    padding-top: 0;
    padding-bottom: 0;
    margin-left: auto;
    border: 0;
  }
}
@media (min-width: 992px) {
  .footer__wrapper {
    padding-top: 71px;
    padding-bottom: 83px;
  }

  .footer__menu {
    margin-bottom: 0;
  }

  .footer__wrapper-xl {
    padding-top: 84px;
  }

  .footer__logo {
    position: relative;
    padding-right: 24px;
    margin-right: 24px;
  }
  .footer__logo::before {
    content: "";
    position: absolute;
    right: 0;
    top: 50%;
    width: 1px;
    height: 32px;
    background-color: var(--gray-hell);
    transform: translateY(-50%);
  }
}
.menu__list {
  display: grid;
  grid-gap: 10px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.menu__link {
  display: block;
  font-size: 16px;
  line-height: 150%;
  color: var(--textColor);
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}
.menu__link:hover, .menu__link:focus {
  color: black;
}

@media (min-width: 992px) {
  .menu__link {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 11px;
    padding-bottom: 11px;
  }
}
.services {
  position: relative;
}

.services__svg {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  color: var(--textColor);
  background-color: var(--gray-hell);
  border-radius: 50%;
  transition: color 0.3s ease-in-out;
}
.services__svg:hover, .services__svg:focus {
  color: black;
}

.services__link {
  font-size: 16px;
  line-height: 24px;
  color: var(--textColor);
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}
.services__link:hover, .services__link:focus {
  color: black;
}

.services__list {
  position: absolute;
  top: calc(100% + 8px);
  right: 0;
  grid-gap: 16px;
  grid-template-columns: repeat(3, 54px);
  padding: 30px;
  width: 258px;
  max-height: 320px;
  background-color: var(--white);
  border-radius: 10px;
  overflow: hidden;
  overflow-y: auto;
  filter: drop-shadow(0px 8px 37px rgba(0, 0, 0, 0.11));
  display: none !important;
}

.services__item {
  display: grid;
  justify-items: center;
  grid-gap: 16px;
  font-size: 14px;
  line-height: 20px;
  color: var(--textColor);
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}
.services__item:hover, .services__item:focus {
  color: black;
}

.services__img-wrapper {
  display: block;
  width: 36px;
  height: 36px;
  background-color: var(--gray);
  border-radius: 50%;
}

.services__title {
  margin-right: 8px;
}

.overlay {
  position: fixed;
  right: 0;
  bottom: 0;
  z-index: 0;
  background: rgba(0, 0, 0, 0.5);
  visibility: hidden;
  opacity: 0;
  transform: left 0.3s ease-in-out, visibility 0.3s ease-in-out, opacity 0.4s ease-in-out;
}

.overlay--active {
  top: 0;
  left: 0;
  opacity: 1;
  visibility: visible;
}

.top__wrapper {
  padding-top: 20px;
}
.top__wrapper:not(.top__wrapper:last-child) {
  margin-bottom: 100px;
}

.top__title {
  margin-top: 0;
  margin-bottom: 30px;
  font-weight: 600;
  font-size: 24px;
  line-height: 133%;
  color: var(--dark-blue);
}

.top__text {
  margin-top: 0;
  font-size: 14px;
  line-height: 143%;
  color: var(--textColor);
}
.top__text p {
  margin-top: 0;
  margin-bottom: 10px;
}

.top__text--main {
  margin-bottom: 23px;
  font-size: 16px;
  line-height: 150%;
}

.top__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.top__list-item {
  position: relative;
  padding-left: 39px;
}
.top__list-item:not(:last-child) {
  margin-bottom: 26px;
}
.top__list-item:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  width: 24px;
  height: 24px;
  background-image: url("/img/icon-tick.svg");
  background-repeat: no-repeat;
}

.top__content {
  margin-bottom: 22px;
}

.top {
  margin-bottom: 100px;
}

@media (min-width: 768px) {
  .top__wrapper:nth-child(2n) .top__img-wrapper {
    padding-right: 12px;
  }

  .top__wrapper:nth-child(2n+1) .top__img-wrapper {
    padding-left: 12px;
  }
}
@media (min-width: 992px) {
  .top {
    padding-top: 132px;
    margin-bottom: 150px;
  }

  .top__wrapper {
    padding-top: 0;
  }

  .top__title {
    margin-bottom: 40px;
    font-size: 32px;
    line-height: 1;
  }

  .top__text--main {
    margin-bottom: 23px;
    font-size: 20px;
    line-height: 150%;
  }

  .top__text {
    font-size: 16px;
    line-height: 150%;
  }

  .top__list {
    font-size: 16px;
    line-height: 150%;
  }

  .top__content {
    margin-bottom: 0;
  }

  .top__wrapper:not(.top__wrapper:last-child) {
    margin-bottom: 185px;
  }

  .top__wrapper:nth-child(2n+1) .top__content {
    padding-right: 69px;
  }

  .top__wrapper:nth-child(2n) .top__content {
    padding-left: 69px;
  }
}
.slider {
  padding-top: 20px;
  margin-bottom: 100px;
}

.slider__wrapper {
  position: relative;
}

.slider__item {
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 32px;
  padding-bottom: 30px;
  height: 100%;
  color: var(--white);
  background-color: var(--blue);
  border-radius: 15px;
}

.slider__name {
  margin-bottom: 21px;
  font-weight: 800;
  font-size: 16px;
  line-height: 125%;
}
.slider__name p {
  margin-top: 0;
  margin-bottom: 21px;
  font-weight: 600;
  font-size: 12px;
  line-height: 133%;
}

.slider__list {
  height: 100%;
}

.slider__wrapper-inside {
  margin-bottom: 38px;
  border-radius: 15px;
  box-shadow: 0px 8px 37px rgba(0, 0, 0, 0.11);
}

.slider__img-wrapper {
  width: 158px;
  height: 96px;
  margin-bottom: 21px;
}

.slider__dots {
  grid-gap: 11.5px;
  margin-bottom: 28.8px;
}

.slider__dot {
  display: block;
  width: 24px;
  height: 4px;
  background-color: var(--blue-light);
  opacity: 0.5;
  transition: opacity 0.3s ease-in-out;
}
.slider__dot:hover, .slider__dot:focus {
  opacity: 0.9;
}

.slider__dot.active {
  opacity: 1;
}

.slider__arrow {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 42.3px;
  height: 42.3px;
  background: var(--white);
  border-radius: 50%;
  box-shadow: 0px 5.34983px 24.743px rgba(0, 0, 0, 0.11);
  cursor: pointer;
  transition: box-shadow 0.3s ease-in-out;
}
.slider__arrow:hover, .slider__arrow:focus {
  box-shadow: 0px 5.34983px 24.743px rgba(0, 0, 0, 0.5);
}
.slider__arrow:active {
  box-shadow: 0px 1.34983px 2.743px rgba(0, 0, 0, 0.11);
}

.slider__arrow--left {
  transform: scale(-1, 1);
}

.slider__controllers {
  grid-gap: 40.1px;
}

@media (min-width: 992px) {
  .slider {
    padding-top: 0;
    margin-bottom: 150px;
  }

  .slider__wrapper-inside {
    border-radius: 20px;
  }

  .slider__arrow {
    position: absolute;
    top: 50%;
  }

  .slider__arrow--left {
    left: 0;
    transform: scale(-1, 1), translateY(-50%);
  }

  .slider__arrow--right {
    right: 0;
    transform: translateY(-50%);
  }

  .slider__item {
    position: relative;
    background-image: url("/img/back.svg");
    background-position: center left;
    background-size: contain;
    background-repeat: no-repeat;
  }

  .slider__dots {
    margin-bottom: 0;
  }

  .slider__name {
    font-size: 20px;
    line-height: 150%;
  }

  .slider__text {
    padding-right: 20px;
    font-size: 16px;
    line-height: 150%;
  }

  .slider__img-wrapper {
    margin-bottom: 0;
    width: auto;
    height: auto;
  }

  .slider__img {
    width: 307px;
    height: 182px;
  }
}
@media (min-width: 1080px) {
  .slider__item {
    position: relative;
    padding-top: 58px;
    padding-bottom: 58px;
    padding-right: 42px;
    padding-left: 46px;
    background: var(--white);
    background-image: url("/img/back.svg");
    background-position: center left;
    background-size: contain;
    background-repeat: no-repeat;
  }

  .slider__text {
    position: relative;
  }

  .slider__eagle {
    position: absolute;
    top: 37%;
    right: -22%;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 102px;
    height: 102px;
    border-radius: 5px;
    background-color: #4D5769;
  }
  .slider__eagle img {
    display: block;
  }

  .slider__wrapper-inside {
    margin-bottom: 33px;
  }
}
.main-form {
  padding-top: 20px;
  margin-bottom: 100px;
}

.main-form__wrapper {
  padding: 45.57px 23.5px;
  background: var(--white);
  box-shadow: 0px 8px 37px rgba(0, 0, 0, 0.11);
  border-radius: 20px;
}

.form__invalid {
  position: absolute;
  top: -21px;
  right: 0;
  z-index: 3;
  padding: 10px;
  white-space: nowrap;
  box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.11);
  border-radius: 5px;
}

.main-form__title {
  margin-bottom: 27px;
  font-weight: 600;
  font-size: 24px;
  line-height: 133%;
  color: var(--dark-blue);
  text-align: center;
}

.main-form__text {
  padding-right: 10px;
  padding-left: 10px;
  margin-bottom: 49px;
  text-align: center;
  font-size: 14px;
  line-height: 140%;
}

.form__input,
.form__textarea {
  z-index: 0;
  padding-right: 16.34px;
  padding-left: 16.34px;
  padding-top: 11.61px;
  padding-bottom: 12.27px;
  font-family: inherit;
  border: 1px solid var(--gray);
  border-radius: 8px;
}
.form__input::placeholder,
.form__textarea::placeholder {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #D3D6DB;
}

.form__input.invalid,
.form__textarea.invalid {
  border-color: red;
}

.form__name {
  display: block;
  margin-bottom: 8px;
  font-size: 12px;
  line-height: 140%;
  color: var(--gray-dark);
}

.form__label {
  position: relative;
  margin-bottom: 28px;
}
.form__label:last-child {
  margin-bottom: 49px;
}

.form__submit {
  padding: 12px 37px;
  margin-bottom: 17px;
  color: var(--white);
  background-color: var(--blue);
  border-radius: 8px;
}

.form__text {
  font-size: 12px;
  line-height: 140%;
  color: var(--blue);
  text-align: center;
}

@media (min-width: 992px) {
  .main-form {
    padding-top: 0;
    margin-bottom: 190px;
  }

  .main-form__wrapper {
    padding-top: 101px;
    padding-bottom: 101px;
    padding-right: 202px;
    padding-left: 202px;
    border-radius: 20px;
  }

  .main-form__title {
    margin-bottom: 27px;
    font-size: 32px;
    line-height: 1;
  }

  .main-form__text {
    margin-bottom: 50px;
    margin-left: auto;
    margin-right: auto;
    max-width: 586px;
    font-size: 14px;
    line-height: 140%;
  }

  .form {
    justify-content: space-between;
  }

  .form__label {
    flex-basis: calc(50% - 11px);
  }

  .form__label:last-of-type,
.form__label:nth-last-of-type(2) {
    flex-basis: 100%;
  }

  .form__submit {
    margin-left: auto;
    margin-right: auto;
  }
}
.footer-menu {
  padding: 0;
  margin-top: 0;
  margin-left: 0;
  margin-right: 0;
  list-style: none;
}

.footer-menu__link {
  display: block;
  font-size: 10px;
  line-height: 120%;
  color: var(--textColor);
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}
.footer-menu__link:hover, .footer-menu__link:focus {
  color: black;
}

.footer-menu__item:not(:last-child) {
  margin-bottom: 14px;
}

@media (min-width: 768px) {
  .footer-menu__link {
    font-size: 14px;
    line-height: 143%;
  }
}
.adress p {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 10px;
  line-height: 140%;
  color: var(--textColor);
}

.adress__name {
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 140%;
  color: var(--textColor);
}

.adress__email {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 10px;
  line-height: 140%;
  color: var(--textColor);
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}
.adress__email:hover, .adress__email:focus {
  color: black;
}

@media (min-width: 768px) {
  .adress p, .adress__email, .adress__name {
    font-size: 14px;
    line-height: 143%;
  }
}