.header {
    position: relative;
}

.header__container {
    padding-top: 20px;
    padding-bottom: 20px;
}

@media (max-width: 991px) {
    .header__toggler {
        width: 23px;
        height: 23px;
        padding: 0;
        margin: 0;
        background-color: transparent;
        border: 0;

        & > span {
            position: relative;
            display: grid;
            grid-gap: 4px;
            align-content: center;
            width: 100%;
            height: 100%;
            box-sizing: border-box;

            span {
                display: block;
                width: 23px;
                height: 2px;
                background-color: var(--textColor);          
            }
        }
    }

    .header__menu-wrapper {
        position: fixed;
        top: 0;
        left: -300px;
        z-index: 1;
        padding-left: 30px;
        padding-right: 30px;
        padding-top: 92px;
        width: 257px;
        min-height: 100vh;
        background-color: var(--white);
        transition: left 0.3s ease-in-out;
    }

    .header__logo {
        width: 146px;
        height: 31px;
    }

    .header__menu {
        margin-bottom: 48px;
    }

    .header__mobile {
        p {
            margin-top: 0;
            margin-bottom: 4px;
            color: var(--blue-middle);
        }

        a {
            display: block;
            margin-bottom: 4px;
            color: var(--blue-middle);
            text-decoration: none;
            transition: color 0.3s ease-in-out;

            &:hover,
            &:focus {
                color: var(--dark-blue);
            }
        }
    }

    .header.opened {
        .header__menu-wrapper {
            left: 0;
        }

        .header__logo {
            z-index: 3;
            width: 86px;
            height: 21px;
            margin-left: 43px;
        }

        .header__toggler {
            position: fixed;
            top: 23px;
            left: 28px;
            z-index: 3;
        } 

        .header__toggler > span {
        & span:nth-child(1) {
            display: none;
        } 

        & span:nth-child(2) {
            position: absolute;
            top: 9px;
            left: 2px;
            width: 20px;
            z-index: 3;
            transform: rotate(45deg);
        }

        & span:nth-child(3) {
                position: absolute;
                top: 9px;
                right: 1px;
                width: 20px;
                z-index: 3;
                transform: rotate(-45deg);
            }
        }
    }
}

@media (min-width: 992px) {
    .header {
        box-shadow: var(--box-shadow);
    }
    
    .header__container {
        padding-top: 21px;
        padding-bottom: 12px;
    }

    .header__logo {
        width: 243px;
        height: 57px;
    }
}