.footer {
    background-color: var(--white);
    box-shadow: 0px 8px 36px rgba(0, 0, 0, 0.14);
}

.footer__wrapper {
    padding-top: 40px;
    padding-bottom: 37px;
}

.footer__adress {
    padding-top: 40px;
    padding-bottom: 48px;
    border-top: 2px solid var(--gray-hell);
}

.footer__menu {
    margin-bottom: 33px;
}

@media (min-width: 768px) {
    .footer__adress {
        padding-top: 0;
        padding-bottom: 0;
        margin-left: auto;
        border: 0;
    }
}

@media (min-width: 992px) {
    .footer__wrapper {
        padding-top: 71px;
        padding-bottom: 83px;
    }

    .footer__menu {
        margin-bottom: 0;
    }

    .footer__wrapper-xl {
        padding-top: 84px;
    }

    .footer__logo {
        position: relative;
        padding-right: 24px;
        margin-right: 24px;

        &::before {
            content: '';
            position: absolute;
            right: 0;
            top: 50%;
            width: 1px;
            height: 32px;
            background-color: var(--gray-hell);
            transform: translateY(-50%);
        }
    }
}