.slider {
    padding-top: 20px;
    margin-bottom: 100px;
}

.slider__wrapper {
    position: relative;
}

.slider__item {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 32px;
    padding-bottom: 30px;
    height: 100%;
    color: var(--white);
    background-color: var(--blue);
    border-radius: 15px;
    
}

.slider__name {    
    margin-bottom: 21px;
    font-weight: 800;
    font-size: 16px;
    line-height: 125%;

    p {
        margin-top: 0;
        margin-bottom: 21px;
        font-weight: 600;
        font-size: 12px;
        line-height: 133%;
    }
}

.slider__list {
    height: 100%;
}

.slider__wrapper-inside {
    margin-bottom: 38px;
    border-radius: 15px;
    box-shadow: 0px 8px 37px rgba(0, 0, 0, 0.11);
}

.slider__img-wrapper {
    width: 158px;
    height: 96px;
    margin-bottom: 21px;;
}

.slider__dots {
    grid-gap: 11.5px;
    margin-bottom: 28.8px;
}

.slider__dot {
    display: block;    
    width: 24px;
    height: 4px;
    background-color: var(--blue-light);
    opacity: 0.5;
    transition: opacity 0.3s ease-in-out;

    &:hover,
    &:focus {
        opacity: 0.9;
    }
}

.slider__dot.active {
    opacity: 1;
}

.slider__arrow {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 42.3px;
    height: 42.3px;
    background: var(--white);
    border-radius: 50%;    
    box-shadow: 0px 5.34983px 24.743px rgba(0, 0, 0, 0.11);
    cursor: pointer;
    transition: box-shadow 0.3s ease-in-out;

    &:hover,
    &:focus {
        box-shadow: 0px 5.34983px 24.743px rgba(0, 0, 0, 0.5);
    }

    &:active {
        box-shadow: 0px 1.34983px 2.743px rgba(0, 0, 0, 0.11);
    }
}

.slider__arrow--left {
    transform: scale(-1,1);
}

.slider__controllers {
    grid-gap: 40.1px;
}

@media (min-width: 992px) {
    .slider {
        padding-top: 0;
        margin-bottom: 150px;
    }

    .slider__wrapper-inside {
        border-radius: 20px;
    }

    .slider__arrow {
        position: absolute;
        top: 50%;
        
    }

    .slider__arrow--left {
        left: 0;
        transform: scale(-1,1), translateY(-50%);
    }

    .slider__arrow--right {
        right: 0;
        transform: translateY(-50%);
    }

    .slider__item {
        position: relative;        
        background-image: url('/img/back.svg');
        background-position:center left;
        background-size: contain;
        background-repeat: no-repeat;
    }

    .slider__dots {
        margin-bottom: 0;
    }

    .slider__name {
        font-size: 20px;
        line-height: 150%;
    }

    .slider__text {
        padding-right: 20px;
        font-size: 16px;
        line-height: 150%;
    }

    .slider__img-wrapper {
        margin-bottom: 0;
        width: auto;
        height: auto;
    }

    .slider__img {
        width: 307px;
        height: 182px;
    }
}

@media (min-width: 1080px) {
    .slider__item {
        position: relative;
        padding-top: 58px;
        padding-bottom: 58px;
        padding-right: 42px;
        padding-left: 46px;
        background: var(--white);
        background-image: url('/img/back.svg');
        background-position:center left;
        background-size: contain;
        background-repeat: no-repeat;
    }
    
    .slider__text  {
        position: relative;
    }

    .slider__eagle {
        position: absolute;
        top: 37%;
        right: -22%;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 102px;
        height: 102px;
        border-radius: 5px;
        background-color: #4D5769;

        img {
            display: block;
        }
    }

    .slider__wrapper-inside {
        margin-bottom: 33px;
    }
}