.main-form {
    padding-top: 20px;
    margin-bottom: 100px;;
}

.main-form__wrapper {
    padding: 45.57px 23.5px;
    background: var(--white);
    box-shadow: 0px 8px 37px rgba(0, 0, 0, 0.11);
    border-radius: 20px;
}

.form__invalid {
    position: absolute;
    top: -21px;
    right: 0;
    z-index: 3;
    padding: 10px;
    white-space: nowrap;
    box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.11);
    border-radius: 5px;
}

.main-form__title {
    margin-bottom: 27px;
    font-weight: 600;
    font-size: 24px;
    line-height: 133%;
    color: var(--dark-blue);
    text-align: center;
}

.main-form__text {
    padding-right: 10px;
    padding-left: 10px;
    margin-bottom: 49px;
    text-align: center;
    font-size: 14px;
    line-height: 140%;
}

.form__input,
.form__textarea {
    z-index: 0;
    padding-right: 16.34px;
    padding-left: 16.34px;
    padding-top: 11.61px;
    padding-bottom: 12.27px;
    font-family: inherit;    
    border: 1px solid var(--gray);
    border-radius: 8px;

    &::placeholder {
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        color: #D3D6DB;
    }
}

.form__input.invalid,
.form__textarea.invalid {
    border-color: red;
}

.form__name {
    display: block;
    margin-bottom: 8px;
    font-size: 12px;
    line-height: 140%;
    color: var(--gray-dark);
}

.form__label {
    position: relative;
    margin-bottom: 28px;

    &:last-child {
        margin-bottom: 49px;
    }
}



.form__submit {
    padding: 12px 37px;
    margin-bottom: 17px;
    color: var(--white);
    background-color: var(--blue);
    border-radius: 8px;
}

.form__text {
    font-size: 12px;
    line-height: 140%;
    color: var(--blue);
    text-align: center;
}

@media (min-width: 992px) {
    .main-form {
        padding-top: 0;
        margin-bottom: 190px;
    }
    
    .main-form__wrapper {
        padding-top: 101px;
        padding-bottom: 101px;
        padding-right: 202px;
        padding-left: 202px;
        border-radius: 20px;
    }

    .main-form__title {
        margin-bottom: 27px;
        font-size: 32px;
        line-height: 1;
    }

    .main-form__text {
        margin-bottom: 50px;
        margin-left: auto;
        margin-right: auto;
        max-width: 586px;
        font-size: 14px;
        line-height: 140%;
    }

    .form {
        justify-content: space-between;
    }

    .form__label {
        flex-basis: calc(50% - 11px);
    }

    .form__label:last-of-type,
    .form__label:nth-last-of-type(2) {
        flex-basis: 100%;
    }

    .form__submit {
        margin-left: auto;
        margin-right: auto;
    }
}