.menu__list {
    display: grid;
    grid-gap: 10px;
    margin: 0;
    padding: 0;
    list-style: none;
}


.menu__link {
    display: block;
    font-size: 16px;
    line-height: 150%;
    color: var(--textColor);
    text-decoration: none;
    transition: color 0.3s ease-in-out;

    &:hover,
    &:focus {
        color: black;
    }
}

@media (min-width: 992px) {
    .menu__link {
        padding-left: 20px;
        padding-right: 20px;
        padding-top: 11px;
        padding-bottom: 11px;
    }
}