.services {
    position: relative;
}

.services__svg {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 25px;
    height: 25px;
    color: var(--textColor);
    background-color: var(--gray-hell);
    border-radius: 50%;
    transition: color 0.3s ease-in-out;

    &:hover,
    &:focus {
        color: black;
    }
}

.services__link {
    font-size: 16px;
    line-height: 24px;
    color: var(--textColor);
    text-decoration: none;
    transition: color 0.3s ease-in-out;

    &:hover,
    &:focus {
        color: black;
    }
}

.services__list {
    position: absolute;
    top: calc(100% + 8px);
    right: 0;
    grid-gap: 16px;
    grid-template-columns: repeat(3, 54px);    
    padding: 30px;
    width: 258px;
    max-height: 320px;
    background-color: var(--white);
    border-radius: 10px;
    overflow: hidden;
    overflow-y: auto;
    filter: drop-shadow(0px 8px 37px rgba(0, 0, 0, 0.11));
    
    display: none !important;
}

.services__item {
    display: grid;
    justify-items: center;
    grid-gap: 16px;
    font-size: 14px;
    line-height: 20px;
    color: var(--textColor);
    text-decoration: none;
    transition: color 0.3s ease-in-out;

    &:hover,
    &:focus {
        color: black;
    }
}

.services__img-wrapper {
    display: block;
    width: 36px;
    height: 36px;
    background-color: var(--gray);
    border-radius: 50%;
}

.services__title {
    margin-right: 8px;
}