.footer-menu {
    padding: 0;
    margin-top: 0;
    margin-left: 0;
    margin-right: 0;
    list-style: none;
}

.footer-menu__link {
    display: block;
    font-size: 10px;
    line-height: 120%;
    color: var(--textColor);
    text-decoration: none;
    transition: color 0.3s ease-in-out;

    &:hover,
    &:focus {
        color: black
    }
}

.footer-menu__item:not(:last-child) {
    margin-bottom: 14px;
}

@media (min-width: 768px) {
    .footer-menu__link {
        font-size: 14px;
        line-height: 143%;
    }    
}